<template>
  <div class="login text-center ">
    <div class="bg-primary-gradient">
      <div class="logo">
        <v-img
            :src="require('@/assets/logo-white.png')"
            contain
            height="176"
            class="mb-5"
        />
      </div>
      <div class="title-primary">
        <div class="title">Cambiar Contraseña</div>
      </div>

      <div style="padding: 0 3rem">
        <v-text-field
            v-model="password"
            label="Nueva Contraseña"
            type="password"
            filled
            append-icon="mdi-lock"
            color="white"
            background-color="white"
        ></v-text-field>
        <v-text-field
            v-model="password_confirmation"
            label="Repetir Contraseña"
            type="password"
            filled
            append-icon="mdi-lock"
            color="white"
            background-color="white"
        ></v-text-field>
      </div>

      <div class="my-5">
        <v-btn width="240" :loading="isLoading" @click="onSave">Guardar contraseña</v-btn>
      </div>
      <div>
        <v-btn width="240" :to="{name:'Home'}">Volver al inicio</v-btn>
      </div>
    </div>
    <svg width="100%" height="124" viewBox="0 0 100 100" preserveAspectRatio="none">
      <defs>
        <polygon id="r1" points="0,0 100,0 70.8,100 0,22.5"/>
        <linearGradient id="g1" x1="0%" y1="0%" x2="100%" y2="0%">
          <stop stop-color="#0084CA" offset="0%"/>
          <stop stop-color="#00406C" offset="100%"/>
        </linearGradient>
      </defs>
      <use x="0" y="0" xlink:href="#r1" fill="url(#g1)"/>
    </svg>
  </div>
</template>
<script>
import axios from 'axios';

export default {
  name: 'ChangePassword',
  inject: ['notyf'],
  data: () => ({
    isLoading: false,
    password: '',
    password_confirmation: '',
  }),
  created() {

  },
  methods: {
    onSave() {
      this.isLoading = true

      let data = {password: this.password, password_confirmation: this.password_confirmation}

      axios.post('profile', data).then(() => {
        this.notyf.success('Contraseña actualizado correctamente.');
        this.$router.push({name: 'Home'})
      }).catch((error) => {
        this.notyf.error(error.response.data.message);
      }).then(() => {
        this.isLoading = false
      })
    },
  }
}
</script>

<style lang="scss" scoped>
.login {
  color: white;

  .logo {
    padding-top: 10%;
  }

  .title-primary {
    padding: 2rem 0;
  }
}
</style>